.demo2__possiblity{
    display: flex;
    flex-direction: row;
}

.demo2__possiblity-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;


}

.demo2__possiblity-image img{
    width: 100%;
    height: 100%;
}

.demo2__possiblity-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

}

.demo2__possiblity-content h4 {
font-family: var(--font-family);
font-weight: 500;
font-size: 16px;
line-height: 30px;
color: var(--color-text);
}